<template>
  <form @submit.prevent="onsubmit()">
    <va-input
      v-model="username"
      type="username"
      :label="$t('auth.username')"
      :error="!!usernameErrors.length"
      :error-messages="usernameErrors"
    />

    <!-- <va-input
      v-model="email"
      type="email"
      :label="$t('auth.email')"
      :error="!!emailErrors.length"
      :error-messages="emailErrors"
    />-->

    <va-input
      v-model="password"
      type="password"
      :label="$t('auth.password')"
      :error="!!passwordErrors.length"
      :error-messages="passwordErrors"
    />

    <div class="auth-layout__options d-flex align--center justify--space-between">
      <va-checkbox
        v-model="agreedToTerms"
        class="mb-0"
        :error="!!agreedToTermsErrors.length"
        :errorMessages="agreedToTermsErrors"
      >
        <template slot="label">
          {{ $t('auth.agree') }}
          <span class="link">{{ $t('auth.termsOfUse') }}</span>
        </template>
      </va-checkbox>
      <!-- <router-link class="ml-1 link" :to="{name: 'recover-password'}">
        {{$t('auth.recover_password')}}
      </router-link>-->
    </div>

    <div class="d-flex justify--center mt-3">
      <va-button type="submit" class="my-0">{{ $t('auth.sign_up') }}</va-button>
    </div>

    <div class="d-flex justify--center mt-3">
      <label>{{registerstatus}}</label>
    </div>
  </form>
</template>

<script>
// import Child from '@/components/auth/take-survey/take-survey.vue';
export default {
  name: 'signup',
  data () {
    return {
      username: '',
      // email: '',
      password: '',
      agreedToTerms: false,
      usernameErrors: [],
      // emailErrors: [],
      passwordErrors: [],
      agreedToTermsErrors: [],
    }
  },
  methods: {
    onsubmit () {
      this.usernameErrors = this.username ? [] : ['username is required']
      // this.emailErrors = this.email ? [] : ['Email is required']
      this.passwordErrors = this.password ? [] : ['Password is required']
      this.agreedToTermsErrors = this.agreedToTerms
        ? []
        : ['You must agree to the terms of use to continue']
      if (!this.formReady) {
        return
      }
      const username = this.username
      const password = this.password
      this.$store
        .dispatch('signupapi', { username, password })
        .catch(err => {
          console.log(err)
          this.snackbar = true
        })
    },
  },
  computed: {
    registerstatus () {
      return this.$store.state.app.registerstatus
    },
    formReady () {
      // return !(this.emailErrors.length || this.passwordErrors.length || this.agreedToTermsErrors.length)
      return !(
        this.usernameErrors.length ||
        this.passwordErrors.length ||
        this.agreedToTermsErrors.length
      )
    },
  },
}
</script>

